import { useMapActions, useMapState } from 'src/hooks'
import { ticketsThunks } from 'src/thunks'
import { ticketsSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      ticket: ticketsSelectors.getTicket(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    openTicket: ticketsThunks.openTicket,
    sendTicketMessage: ticketsThunks.sendTicketMessage,
  })
}
