import React, { memo } from 'react'

import styles from './styles.module.scss'

const EmptyRaw = ({ isManager, showCreateTicketPopup }) => {
  return (
    <div className={styles.empty}>
      <div className={styles.label}>
        <div className={styles.descr}>Select the chat on the left{isManager ? '.' : ','}</div>
        {!isManager && (
          <div className={styles.descrUser}>
            or <button onClick={showCreateTicketPopup}>Open new ticket</button>
          </div>
        )}
      </div>
    </div>
  )
}

export const Empty = memo(EmptyRaw)
export default Empty
