import React, { memo, useMemo, useCallback, useState, useEffect } from 'react'
import cn from 'classnames'
import moment from 'moment'

import { modalConstants } from 'src/constants'

import { ReactComponent as AddIcon } from 'src/assets/images/icons/new.svg'
import PageLayout from 'src/components/PageLayout'
import BaseButton from 'src/components/BaseButton'
import Empty from './components/Empty'
import Chat from './components/Chat'

import { useMappedActions, useMappedState } from './bindings'

import styles from './styles.module.scss'

const TABS = [
  { key: 'OPEN', name: 'Open tickets' },
  { key: 'CLOSED', name: 'Closed tickets' },
]

const SupportRaw = () => {
  const [activeTab, setActiveTab] = useState(TABS[0])
  const [activeTicket, setActiveTicket] = useState(null)
  const actions = useMappedActions()
  const { all, isClosingTicket, isManager } = useMappedState()
  const userId = `854a312b-ae6a-4d2d-8224-d912a9ddb5a7-${isManager ? 'manager' : 'user'}`

  useEffect(() => {
    actions.loadTickets()
  }, [actions])

  const sortTickets = useMemo(() => {
    const nextData = {
      OPEN: [],
      CLOSED: [],
    }
    all.data.forEach((item) => {
      const { status } = item
      if (status === 'opened') {
        nextData.OPEN.push(item)
      }
      if (status === 'closed') {
        nextData.CLOSED.push(item)
      }
    })

    return nextData
  }, [all])

  const currTickets = useMemo(() => {
    return sortTickets[activeTab.key]
  }, [sortTickets, activeTab])

  const showCreateTicketPopup = () => {
    actions.showModal(modalConstants.CREATE_TICKET)
  }

  const onClickItem = useCallback(
    (ticket_id) => {
      setActiveTicket(ticket_id)
    },
    [setActiveTicket],
  )

  const onClickResolve = useCallback(
    (e, id) => {
      e.preventDefault()
      e.stopPropagation()
      actions.closeTicket(id)
    },
    [actions],
  )

  const onClickTab = useCallback((tab) => {
    setActiveTab(tab)
    setActiveTicket(null)
  }, [])

  const renderTabs = useMemo(() => {
    return TABS.map((tab) => {
      return (
        <button
          key={tab.key}
          onClick={() => onClickTab(tab)}
          className={cn(styles.tab, {
            [styles.tabActive]: tab.key === activeTab.key,
          })}
        >
          {tab.name}
        </button>
      )
    })
  }, [activeTab, onClickTab])

  const renderTickets = useMemo(() => {
    return currTickets.map((item, index) => {
      const { subject, message, id } = item
      const { text, author, created: createdLastMessage } = message
      const isOwnMessage = userId === author.id
      const momentDate = moment(createdLastMessage)

      return (
        <li
          className={cn(styles.item, {
            [styles.itemActive]: id === activeTicket,
          })}
          key={id}
          onClick={() => onClickItem(id)}
        >
          <div className={styles.itemTop}>
            <div className={styles.date}>{momentDate.format('YYYY-MM-DD hh:mm')}</div>
            {isManager && (
              <button className={styles.resolved} onClick={(e) => onClickResolve(e, id)}>
                Resolved
              </button>
            )}
          </div>
          <div className={styles.title}>{subject}</div>
          <div className={styles.author}>
            <span className={styles.type}>Support:</span> <span className={styles.name}>Alexander</span>
          </div>
          <div className={styles.message}>
            {isOwnMessage && <b>You: </b>}
            {text}
          </div>
        </li>
      )
    })
  }, [currTickets, activeTicket, onClickItem, onClickResolve, isManager, userId])

  return (
    <PageLayout pageTitle={'Support'}>
      <div className={styles.root}>
        <div className={styles.left}>
          <div className={styles.tools}>
            <div className={styles.tabs}>{renderTabs}</div>
            {!isManager && (
              <div className={styles.new}>
                <BaseButton
                  text={
                    <span className={styles.delete}>
                      <AddIcon className={styles.addIcon} /> <span>Open new ticket</span>
                    </span>
                  }
                  skin="yellow"
                  size="sm"
                  disabled={false}
                  onClick={showCreateTicketPopup}
                />
              </div>
            )}
          </div>
          <ul className={styles.list}>{renderTickets}</ul>
        </div>
        <div className={styles.right}>
          {activeTicket ? (
            <Chat activeTicket={activeTicket} userId={userId} />
          ) : (
            <Empty showCreateTicketPopup={showCreateTicketPopup} isManager={isManager} />
          )}
        </div>
      </div>
    </PageLayout>
  )
}

export const Support = memo(SupportRaw)
export default Support
