import React, { memo, useEffect, useMemo, useCallback, useRef } from 'react'
import cn from 'classnames'
import moment from 'moment'

import FullLoader from 'src/components/FullLoader'

import { ReactComponent as SendIcon } from 'src/assets/images/icons/send.svg'

import { useMappedActions, useMappedState } from './bindings'

import styles from './styles.module.scss'

const ChatRaw = ({ activeTicket, userId }) => {
  const actions = useMappedActions()
  const { ticket } = useMappedState()
  const { data, isLoading, isSendingMessage, id } = ticket
  const { messages = [] } = data
  const inputRef = useRef(null)

  useEffect(() => {
    actions.openTicket(activeTicket)
  }, [actions, activeTicket])

  const onSend = useCallback(async () => {
    if (inputRef && inputRef.current) {
      await actions.sendTicketMessage(id, {
        message: inputRef.current.textContent,
      })
      inputRef.current.textContent = ''
    }
  }, [actions])

  const renderFiles = useCallback((files) => {
    if (files) {
      return files.map((file, index) => {
        const { name, mime, link } = file
        if (mime.includes('image/')) {
          return (
            <div className={styles.chatImg} key={mime + index}>
              <img src={link} alt={name} />
            </div>
          )
        } else {
          return (
            <div className={styles.chatDoc} key={mime + index}>
              <a href={link} target="_blank" rel="noopener noreferrer">
                {name}
              </a>
            </div>
          )
        }
      })
    }
    return null
  }, [])

  const renderMessages = useMemo(() => {
    return messages.map((message) => {
      const { text, files, author = {}, created } = message
      const isOwnMessage = userId === author.id
      const momentDate = moment(created)
      return (
        <div
          key={created}
          className={cn(styles.message, {
            [styles.messageOwn]: isOwnMessage,
          })}
        >
          <div className={styles.inner}>
            <div className={styles.head}>
              <div className={styles.name}>{isOwnMessage ? 'You' : author.name}</div>
              <div className={styles.date}>{momentDate.format('YYYY-MM-DD hh:mm')}</div>
            </div>
            <div className={styles.content}>
              <div className={styles.chatText}>{text}</div>

              {renderFiles(files)}
              {/* <p>Dear partner,</p>
              <p>
                In order to comply with LeadsGate rules and comply with KYC / AML policies, we ask you to update your
                personal information in your profile, as well as verify your payment information.
              </p>
              <p>
                {' '}
                Please note that payments are only possible to accounts belonging to the owner of the LeadsGate account.{' '}
              </p>
              <p>Double-check the correctness of the information in your profile.</p>
              <p>
                Also, to confirm the entered payment data, please provide a photo of the screen of your payment account,
                containing the account id, name and address of the account holder.
              </p>
              <p>Thank you for your understanding and your time.</p> */}
            </div>
          </div>
        </div>
      )
    })
  }, [messages, renderFiles, userId])

  const onChangeInput = (value) => {
    console.log('onChangeInput value', value)
  }

  return (
    <div className={styles.root}>
      <div className={styles.chat}>{renderMessages}</div>
      <div className={styles.bottom}>
        <div className={styles.field}>
          <div className={styles.input} contentEditable="true" ref={inputRef}></div>
          <button className={styles.send} onClick={onSend}>
            <SendIcon />
          </button>
        </div>
      </div>
      {isLoading && <FullLoader />}
    </div>
  )
}

export const Chat = memo(ChatRaw)
export default Chat
