import { useMapActions, useMapState } from 'src/hooks'
import { ticketsThunks, modalThunks } from 'src/thunks'
import { userSelectors, ticketsSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      all: ticketsSelectors.getAll(state),
      ticket: ticketsSelectors.getTicket(state),
      isClosingTicket: ticketsSelectors.getIsClosingTicket(state),
      isManager: userSelectors.getIsManager(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    showModal: modalThunks.showModal,
    closeTicket: ticketsThunks.closeTicket,
    loadTickets: ticketsThunks.loadTickets,
  })
}
